@use "@app/bancor-components/Container/BaseInternal";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Shadow";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Position";
@use "@app/bancor-components/Element/EnvironmentSelector";

$width-background-trama: 500px;

@mixin headerTrama {
    @include Flex.set($justify: end, $dir: column);
    background-image: var(--trama-background-image);
    background-color: var(--background-trama-color);
    background-position: center;
}

.Header {
    @include Position.set($position: relative);
    @include Color.set($bg: "transparent");
    @include Padding.set($left: beta, $right: beta);

    @include screen.xl {
        @include Margin.set($left: delta, $right: delta);
        @include Position.set($z-index: epsilon);
        grid-row: 1;
        grid-column: 2;
    }

    #{Button.$selector} {
        @include Position.set($z-index: delta);
    }

    &.environmentWidget {
        #{EnvironmentSelector.$selector} {
            div > div:last-child {
                @include screen.lg {
                    @include Position.set($left: none);
                }
                @include screen.xl {
                    @include Position.set($left: initial);
                }
            }
        }
    }

    + .trama {
        height: BaseInternal.$size-header;
    }
}

.headerWidget {
    @include Margin.set($top: none);
    @include Shadow.set($box: alpha);
    @include Border.set($radius-bl: iota, $radius-br: iota);
    width: 100%;
    overflow: hidden;

    @include screen.xl {
        @include Margin.set($left: delta, $right: delta);
        @include Position.set($position: relative);
        width: initial;
        grid-row: 1;
        grid-column: 2;
    }

    &.trama {
        @include headerTrama;
        @include Border.set($radius-bl: beta, $radius-br: beta);
        @include Position.set($position: absolute, $top: none);
        background-size: $width-background-trama;
        @include screen.xl {
            @include Position.set($position: initial);
        }
        overflow: initial;
    }

    .tramaWidgets {
        @include headerTrama;
        @include Position.set($position: absolute);
        @include Position.set($preset: zero);
        height: Layout.get-space(lambda);
        clip-path: ellipse(62% 100% at center top);

        @include screen.xl {
            @include Position.set($z-index: delta);
            clip-path: ellipse(52% 100% at center top);
        }
    }

    .headerWithoutAccounts {
        height: BaseInternal.$size-header;
        @include Border.set($radius-bl: iota, $radius-br: iota);
        clip-path: none;
    }
}
