@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Layout/Position";
@use "@app/bancor-components/Style/screen";

.Footer {
    @include Position.set($position: absolute, $bottom: none);
    width: 100%;
    height: Layout.get-space(kappa);
    background-image: var(--footer-background-movil-image);
    background-size: cover;
    > section {
        z-index: 1;
    }
}

@include screen.xl {
    .Footer {
        background-image: var(--footer-background-image);
        > section {
            @include Display.set($display: none);
        }
    }
}
