@use "@app/bancor-components/Element/Link";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Position";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Element/Image";
@use "@app/bancor-components/Style/Layout/Display";

.Menu {
    @include Padding.set($top: delta, $right: beta, $bottom: delta, $left: beta);
    @include Position.set($position: fixed);

    [role="separator"] {
        @include Color.set($bg: "gray-400");
        @include Margin.set($top: delta, $bottom: delta);
    }

    header {
        @include Margin.set($bottom: beta);
    }

    @include screen.xl {
        @include Position.set($z-index: alpha);
        grid-row: span 2;
        grid-column: 1;
    }
}

.navContainer {
    #{Text.$selector} {
        @include Font.set($size: gamma, $weight: normal, $family: "default");
    }
    & svg {
        @include Margin.set($right: alpha);
    }
}

.buttonDefault {
    @include Margin.set($top: alpha);
    #{Link.$selector} {
        & > path {
            @include Color.set($svg-fg: "gray-600");
        }
        #{Text.$selector} {
            @include Color.set($fg: "gray-dark");
        }
    }
}

.profileNav {
    @include Margin.set($top: alpha, $bottom: beta);
    @include Padding.set($top: beta, $bottom: beta, $webkit-before: delta, $webkit-after: delta);
    @include Flex.set($justify: between, $align: center);
    @include Border.set($radius: delta, $width: alpha, $style: gamma, $color: "gray-400");
    height: Layout.get-size(iota);
    cursor: pointer;

    .alignIconPrimary {
        @include Padding.set($top: beta, $right: beta, $bottom: beta, $left: beta);
        @include Flex.set($justify: space-between, $align: center);
        @include Font.set($deco: default);
        width: 100%;

        .pictureName {
            @include Flex.set($justify: start, $align: center);
            #{Text.$selector} {
                @include Color.set($fg: "gray-dark");
                @include Font.set(
                    $family: heading,
                    $weight: bold,
                    $size: delta,
                    $transform: capitalize
                );
            }

            & > .avatar {
                @include Margin.set($right: alpha);
            }
        }

        .alignIconSecondary {
            @include Flex.set($justify: end);
        }
    }
}

.logoutButton {
    @include Margin.set($top: alpha);
    #{Button.$selector} {
        @include Padding.set($preset: none);
        @include Margin.set($left: alpha);
        @include Flex.set($align: center);
        @include Color.set($bg: "transparent");
        @include Border.set($style: default);
        cursor: pointer;

        // Button svg
        & > svg {
            @include Margin.set($right: alpha);
        }

        // Component Text
        #{Text.$selector} {
            @include Color.set($fg: "gray-dark");
            @include Font.set($size: gamma, $weight: normal, $family: "default");
        }
    }

    #{Text.$selector} {
        @include Color.set($fg: "gray-700");
        @include Font.set($size: alpha, $weight: normal, $family: "default");
    }
}
